@font-face {
    font-family: 'Banny Round';
    src: url('Banny-BoldRound.woff2') format('woff2'),
        url('Banny-BoldRound.woff') format('woff'),
        url('Banny-BoldRound.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Banny Round';
    src: url('Banny-Round.woff2') format('woff2'),
        url('Banny-Round.woff') format('woff'),
        url('Banny-Round.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Banny Light Round';
    src: url('Banny-LightRound.woff2') format('woff2'),
        url('Banny-LightRound.woff') format('woff'),
        url('Banny-LightRound.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

